.banner-view-mng {
  border: 1px solid #ddd;
}
.banner-view-mng .banner-title {
  height: 48px;
  line-height: 48px;
  padding: 0px 15px;
  background-color: #f9f9f9;
  border-bottom: 1px solid #ddd;
}
.banner-view-mng .banner-title .title {
  float: left;
  font-weight: bold;
}
.banner-view-mng .banner-title .title i {
  font-size: 16px;
  font-weight: normal;
  vertical-align: middle;
}
.banner-view-mng .banner-title .btns {
  float: right;
}
.banner-view-mng .banner-content {
  background: #fff;
  height: 100%;
  min-height: 20px;
}
.banner-view-mng .banner-content .notdata {
  text-align: center;
  padding: 10px;
}
.banner-view-mng .banner-content .notdata img {
  opacity: 0.6;
  width: 80px;
}
.banner-view-mng .banner-content .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: top;
      align-items: top;
  margin: 7px;
}
.banner-view-mng .banner-content .preview {
  -ms-flex: auto;
      flex: auto;
  max-height: 124px;
  overflow: hidden;
  float: left;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-right: 7px;
  -webkit-transition: max-height 0.2s ease-in;
  transition: max-height 0.2s ease-in;
}
.banner-view-mng .banner-content .preview:hover {
  max-height: 220px;
}
.banner-view-mng .banner-content .preview img {
  height: 100%;
  border-radius: 4px;
}
.banner-view-mng .banner-content .oper {
  -ms-flex: none;
      flex: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  float: right;
}
.banner-view-mng .banner-content .oper button {
  border-radius: 0;
  margin-top: -1px;
  -webkit-transition: none;
  transition: none;
}
.banner-view-mng .banner-content .oper button:focus,
.banner-view-mng .banner-content .oper button:hover {
  z-index: 2;
}
.banner-view-mng .banner-content .oper > li:first-child button {
  border-radius: 4px 4px 0 0;
}
.banner-view-mng .banner-content .oper > li:last-child button {
  border-radius: 0 0 4px 4px;
}
.banner-view-mng .banner-content .banner-form {
  padding-top: 10px;
  padding-bottom: 20px;
}
.banner-view-mng .banner-content .banner-form .ant-upload-select-picture-card i {
  font-size: 28px;
  margin-bottom: 4px;
}
.banner-view-mng .banner-content .banner-form .ant-upload-list-picture-card .ant-upload-list-item {
  width: 200px;
}
.banner-view-mng .banner-content .banner-form .ant-upload-select-picture-card .ant-upload-text {
  font-size: 14px;
}
.banner-view-mng .banner-content .banner-form .form-btns {
  text-align: left;
  margin-left: 12.4%;
}
.banner-view-mng .banner-content .bannerform-footer button {
  margin-right: 5px;
}
